export default {
	isWxBrowser,
	goWxOauthUrl,
	fuckWx,
	initWxConfig(wxConfig) { // https://developers.weixin.qq.com/doc/offiaccount/OA_Web_Apps/JS-SDK.html#1
		if (isWxBrowser()) {
			jweixin.config({
				debug: wxConfig.debug || false, // ,apialertpclogpc
				appId: wxConfig.appId, // 
				timestamp: wxConfig.timestamp, // 
				nonceStr: wxConfig.nonceStr, // 
				signature: wxConfig.signature, // 
				jsApiList: wxConfig.jsApiList || [
					// 'checkJsApi', // JS
					'updateAppMessageShareData', // ready
					'updateTimelineShareData', // ready
					// 'getLocation', // 
					// 'openLocation', // 
					// 'scanQRCode', // 
					'chooseWXPay' // 
					// 'hideAllNonBaseMenuItem', // : https://developers.weixin.qq.com/doc/offiaccount/OA_Web_Apps/JS-SDK.html#64
					// 'showAllNonBaseMenuItem', 
					// 'hideMenuItems', // 
					// 'showMenuItems'
				], // JS
				openTagList: wxConfig.openTagList || [
					'wx-open-subscribe' //  https://developers.weixin.qq.com/doc/offiaccount/OA_Web_Apps/Wechat_Open_Tag.html#23
				] // ['wx-open-launch-app']
			})
		}
	},
	/**
	 * 
	 */
	updateAppMessageShareData(data, callback, destroyed) {
		if (isWxBrowser() && !destroyed) {
			jweixin.ready(function() { // 
				jweixin.updateAppMessageShareData({ //  https://developers.weixin.qq.com/doc/offiaccount/OA_Web_Apps/JS-SDK.html#10
					title: data.title, // 
					desc: data.desc, // 
					link: data.link || document.location.href, // JS
					imgUrl: data.imgUrl, // 
					success: function() {
						callback()
					}
				})
				jweixin.updateTimelineShareData({ // 
					title: data.title, // 
					link: data.link || document.location.href, // JS
					imgUrl: data.imgUrl, // 
					success: function() {}
				})
			})
		}
		// onHideonUnloaddestroyed
		if (destroyed) {
			jweixin.updateAppMessageShareData({})
			jweixin.updateTimelineShareData({})
		}
	},
	/**
	 * 
	 * https://developers.weixin.qq.com/doc/offiaccount/OA_Web_Apps/JS-SDK.html#58
	 * https://pay.weixin.qq.com/wiki/doc/apiv3/open/pay/chapter1_1_1.shtml
	 */
	chooseWXPay(data, success, cancel, fail) {
		if (isWxBrowser()) {
			jweixin.chooseWXPay({
				timestamp: data.timestamp, // jssdktimestamptimeStampS
				nonceStr: data.nonceStr, //  32 
				package: data.package, // prepay_idprepay_id=\*\*\*
				signType: data.signType, // 'SHA1''MD5'
				paySign: data.paySign, // 
				success: function(res) {
					// 
					success(res)
				},
				// 
				cencel: function(res) {
					cancel(res)
				},
				// 
				fail: function(res) {
					fail(res)
				}
			})
		}
	},
	hideAllNonBaseMenuItem() { // https://developers.weixin.qq.com/doc/offiaccount/OA_Web_Apps/JS-SDK.html#47
		if (isWxBrowser()) {
			jweixin.ready(function() { // 
				jweixin.hideAllNonBaseMenuItem()
			})
		}
	},
	closeWindow() {
		if (isWxBrowser()) {
			jweixin.closeWindow()
		}
	}
}
