import appConfig from '@/config/config.js'

export function getBaseUrl() {
  if (process.env.NODE_ENV === 'development') {




    return appConfig.devHost

  } else {
    return appConfig.prodHost + appConfig.prodApi
  }
}

// Vue.usehttps://uviewui.com/components/vueUse.html
// vmvuethis
// vuextoken
// getApp().globalData
// tokengetApp().globalData
const install = (Vue, vm) => {
  // config
  Vue.prototype.$u.http.setConfig({
    baseUrl: getBaseUrl(),
    showLoading: process.env.NODE_ENV === 'development',
    originalData: true
  })
  // Token
  Vue.prototype.$u.http.interceptor.request = (config) => {
    if (vm.vuex_token) {
      config.header[appConfig.tokenHeadName] = appConfig.tokenTypeName + vm.vuex_token
    }
    return config
  }
  // 
  Vue.prototype.$u.http.interceptor.response = (res) => {
    // originalDatatrue
    // res.statueCoderes.data.code
    // originalDatatruereturn
	// this.$u.postthen
    if (res.statusCode === 200) {
      // 
      const code = res.data.code || 200
      if (code === 401) {
        uni.showModal({
          title: '',
          content: ',',
          showCancel: false,
          success: function(res) {
            if (res.confirm) {
              uni.navigateTo({
                url: '/pages/login/login'
              })
            }
          }
        })
        return false
      } else if (code === 500) {
        // 
        if (process.env.NODE_ENV === 'development') {
          uni.showToast({
            title: '',
            icon: 'none',
            duration: 800
          })
          console.log(res)
        }
        return false
      } else if (code !== 200 && code !== 1) {
        uni.showToast({
          title: res.data.msg,
          icon: 'none',
          duration: 800
        })
        return false
      } else {
        return res.data
      }
    } else {
      // 200modal
      if (res.errMsg) {
        // 
        if (process.env.NODE_ENV === 'development') {
          uni.showToast({
            title: '',
            icon: 'none',
            duration: 800
          })
        }
      }
      return false
    }
  }
}

export default {
  install
}
