// onLoad
export function forceLogin(vuex_token) {
	if (!vuex_token) {
		uni.redirectTo({
			url: '/pages/login/login'
		})
	}
	return !vuex_token
}

// 
export function toLogin(vuex_token) {
	if (!vuex_token) {
		uni.navigateTo({
			url: '/pages/login/login'
		})
	}
	return !vuex_token
}

export function getCurrentRoute() {
	const routes = getCurrentPages() // 
	const curRoute = routes[routes.length - 1].route // 
	const curParam = routes[routes.length - 1].options // 
	// 
	let param = ''
	for (const key in curParam) {
		param += '&' + key + '=' + curParam[key]
	}
	return "/" + curRoute + param.replace('&', '?')
}

export function openURL(url) {
	// http
	if (!/^https?/.test(url)) {
		uni.navigateTo({
			url: url
		})
		return
	}

	// Httpwebview
	uni.navigateTo({
		url: '/pages/container/webview',
		success: function(res) {
			// eventChannel
			res.eventChannel.emit('acceptDataForWebview', {
				data: url
			})
		}
	})
	return








	uni.setClipboardData({
		data: url
	})
	uni.showModal({
		content: '',
		showCancel: false
	})

}

export function formatNumToKw(num) {
	// kw
	return num >= 1e3 && num < 1e4 ? (num / 1e3).toFixed(2) + 'k' : num >= 1e4 ? (num / 1e4).toFixed(2) + 'w' : num
}

export function getSuffix(filename) {
	// 
	const pos = filename.lastIndexOf('.')
	let suffix = ''
	if (pos !== -1) {
		suffix = filename.substring(pos)
	}
	return suffix
}

export function contactKefu(phoneNum) {
	// 
	uni.makePhoneCall({
		phoneNumber: phoneNum || '17688889999' //
	});
}

export default {
	forceLogin,
	toLogin,
	getCurrentRoute,
	openURL,
	formatNumToKw,
	getSuffix,
	contactKefu
}
