import { mapState } from 'vuex'
import store from '@/store'

// store/index.jsvuexstate
let $uStoreKey = []
try {
  $uStoreKey = store.state ? Object.keys(store.state) : []
} catch (e) {

}

module.exports = {
  beforeCreate() {
    // vuex$u
    // vuexstateuser.name"" => this.$u.vuex('user.name', '')
    // vuexstateversion1.0.1 => this.$u.vuex('version', '1.0.1')
    this.$u.vuex = (name, value) => {
      this.$store.commit('$uStore', {
        name,
        value
      })
    }
  },
  computed: {
    // vuexstatemixin
    ...mapState($uStoreKey)
  }
}
