import Vue from 'vue'
import Vuex from 'vuex'
import appConfig from '@/config/config.js'
import { saveStateKeys, appTabbar } from './config.js'
Vue.use(Vuex)

let lifeData = {}

try {
  // lifeDataAPP
  lifeData = uni.getStorageSync('lifeData')
} catch (e) {

}

// APPstate
// const saveStateKeys = ['vuex_user', 'vuex_token']

// 
const saveLifeData = function(key, value) {
  // 
  if (saveStateKeys.indexOf(key) !== -1) {
    // lifeData
    let tmp = uni.getStorageSync('lifeData')
    // APPlifeData{}
    tmp = tmp || {}
    tmp[key] = value
    // lifeData
    uni.setStorageSync('lifeData', tmp)
  }
}

const store = new Vuex.Store({
  state: {
    // lifeDatastate
    // vuex_
    vuex_user: lifeData.vuex_user ? lifeData.vuex_user : { nickName: '' },
    vuex_token: lifeData.vuex_token ? lifeData.vuex_token : '',
    // vuex_versionlifeData.vuex_version
    vuex_name: appConfig.name,
    vuex_version: appConfig.version,
    // tabbar
    vuex_tabbar: appTabbar
  },
  mutations: {
    $uStore(state, payload) {
      // stateuser.info.score = 1
      const nameArr = payload.name.split('.')
      let saveKey = ''
      const len = nameArr.length
      if (len >= 2) {
        let obj = state[nameArr[0]]
        for (let i = 1; i < len - 1; i++) {
          obj = obj[nameArr[i]]
        }
        obj[nameArr[len - 1]] = payload.value
        saveKey = nameArr[0]
      } else {
        // state
        state[payload.name] = payload.value
        saveKey = payload.name
      }
      // 
      saveLifeData(saveKey, state[saveKey])
    }
  }
})

export default store
